import React, { Component } from 'react';
import queryString from "query-string";
import { connect } from 'react-redux';
import { bindActionCreators } from 'redux';
import TeslaIcon from '../../icons/TeslaIcon';
import RdsBackIcon from '../../icons/RdsBackIcon';
import RDSButton from '../../components/common/RDSButton';
import RDSRadioButton from '../../components/common/RDSRadioButton';
import StoreStateConstants from '../../constants/store/StoreStateConstants';
import * as orderActionCreator from '../../actions/orderActionCreator';
import * as TranslationSelectors from '../../selectors/TranslationSelectors';
import LocalStorageUtil from '../../utils/LocalStorageUtil';
import PagePathConstants from '../../constants/router/PagePathConstants';
import { BASE_URL } from '../../constants/api/ApiEndpointConstants';

class OrderConfirmationPage extends Component {
    constructor(props, context) {
        super(props, context);
        let { translations } = props;
        translations = translations && translations['submission_page'];
        this.state = {
            selectedInsuranceType: StoreStateConstants.PAY_ONLINE,
            insurance_types: this.renderInsuranceTypes(translations),
            isSubmitting: false,
        };
        this.routeToSpecPage = this.routeToSpecPage.bind(this);
        this.renderInsuranceTypes = this.renderInsuranceTypes.bind(this);
        this.prefillOrderParams = this.prefillOrderParams.bind(this);
        this.handleSelectPayType = this.handleSelectPayType.bind(this);
        this.toggleSubmitting = this.toggleSubmitting.bind(this);
        this.handleSubmitForm = this.handleSubmitForm.bind(this);
        this.goBack = this.goBack.bind(this);
    }

    componentDidMount() {
        this.prefillOrderParams();
    }

    UNSAFE_componentWillUpdate(nextProps) {
        if (nextProps.translations !== this.props.translations) {
            let { translations } = nextProps;
            translations = translations && translations['submission_page'];
            this.setState({
                insurance_types: this.renderInsuranceTypes(translations)
            })
        }
    }

    goBack() {
        const { history } = this.props;
        history && history.goBack();
    }

    handleSelectPayType(key) {
        this.setState({ selectedInsuranceType: key })
    }

    toggleSubmitting() {
        this.setState({ isSubmitting: !this.state.isSubmitting });
    }

    prefillOrderParams() {
        const orderInfo = LocalStorageUtil.loadOrderDetail();
        this.setState({
            selectedInsuranceType: orderInfo.insuranceType ? orderInfo.insuranceType : this.state.selectedInsuranceType,
        });
    }

    renderInsuranceTypes(translations) {
        return [
            {
                key: StoreStateConstants.PAY_ONLINE,
                label: translations && translations.choice_pay_online.replace('%s', localStorage.getItem('payment_amount')),
            },
            {
                key: StoreStateConstants.PAY_OFFLINE,
                label: translations && translations.choice_pay_offline,
            },
        ];
    }

    UNSAFE_componentWillUpdate(nextProps) {
        if (nextProps.translations !== this.props.translations) {
            let { translations } = nextProps;
            translations = translations && translations['submission_page'];
            this.setState({
                insurance_types: this.renderInsuranceTypes(translations)
            })
        }
    }

    routeToSpecPage(url) {
        const { history, location } = this.props;
        history && history.push({ pathname: url, search: location.search });
    }

    handleSubmitForm() {
        const orderInfo = LocalStorageUtil.loadOrderDetail();
        const {firstName, lastName, email, phone, driveEmail, drivePhone, state, city, country, address, zipCode, vehicleModel, identityType, documentFront, insuranceType, snCode, documentBack, userSelfPictureOrVideo} = orderInfo;
        const { selectedInsuranceType } = this.state;

        let data = {
            firstName,
            lastName,
            email,
            phone,
            driveEmail,
            drivePhone,
            state,
            city,
            country,
            address,
            zipCode,
            vehicleModel,
            identityType,
            documentFront: documentFront && documentFront.replace(BASE_URL, ''),
            userSelfPictureOrVideo: userSelfPictureOrVideo && userSelfPictureOrVideo.replace(BASE_URL, ''),
            insuranceType: selectedInsuranceType,
        }
        if (country === 'US') {
            data['snCode'] = snCode;
        }
        if (data.identityType !== StoreStateConstants.IDENTITY_TYPE_PASSPORT) {
            data['documentBack'] = documentBack.replace(BASE_URL, '');
        }
        const { location } = this.props;
        const queryParams = queryString.parse(location.search);
        if (queryParams.ad && queryParams.ad !== '') {
            data['ad'] = queryParams.ad;
        }
        LocalStorageUtil.saveOrderDetail({insuranceType: selectedInsuranceType, ad: queryParams.ad});
        if (firstName && firstName.trim() !== '' && lastName && lastName.trim() !== '') {
            const { orderActions } = this.props;
            this.toggleSubmitting();
            orderActions && orderActions.submitDriveOrder(data)
                .then((data) => {
                    this.toggleSubmitting();
                    if (data && data.url) {
                        window.location.href = data.url;
                    }
                })
                .catch(this.toggleSubmitting);
        }
    }

    render() {
        let translations = this.props.translations && this.props.translations['submission_page'];
        const orderInfo = LocalStorageUtil.loadOrderDetail();
        const { firstName, lastName, vehicleModel, address, city, country, documentBack, documentFront, driveEmail, drivePhone, email, identityType, insuranceType, phone, snCode, state, userSelfPictureOrVideo, zipCode } = orderInfo;
        const { selectedInsuranceType, isSubmitting } = this.state;

        const disabledSubmitBtn = (!orderInfo || !firstName || firstName.trim() === '' || !lastName || lastName.trim() === '' || !email || email.trim() === '' || !phone || phone.trim() === '' || !address || address.trim() === '' || !state || state.trim() === '' || !city || city.trim() === '' || !zipCode || zipCode.trim() === '' || !country || country.trim() === '' || (country === 'US' && snCode.trim() === '') || !drivePhone || drivePhone.trim() === '' || !driveEmail || driveEmail.trim() === '' || !identityType || identityType.trim() === '' || !documentFront || documentFront.trim() === '' || (identityType !== StoreStateConstants.IDENTITY_TYPE_PASSPORT && (!documentBack || documentBack.trim() === '')) || !userSelfPictureOrVideo || userSelfPictureOrVideo.trim() === '' || isSubmitting)
        return (
            <div className='tw-w-full tw-h-full gotham-font tw-text-[#393c41] '>
                <div className='tw-w-full tw-h-full phone:tw-h-full tw-pb-[200px] tw-overflow-scroll'>
                    <div className='tw-z-[100] tw-w-full tw-h-[80px] tw-flex tw-justify-between'>
                        <div className='tw-w-1/3 tw-h-full tw-flex tw-items-center tw-pl-[30px]'><RdsBackIcon color='#171a20' onClick={this.goBack} /></div>
                        <TeslaIcon size={150} color='#171a20' />
                        <div className='tw-w-1/3'></div>
                    </div>
                    <div className='tw-mt-[66px] tw-w-[800px] tw-mx-auto phone:tw-px-[20px] phone:tw-w-full phone:tw-mt-[36px]'>
                        <div className='tw-text-[30px] tw-leading-[60px] tw-font-[500] phone:tw-text-[24px]'>{translations && translations.order_confrim}</div>
                    </div>

                    <div className='tw-w-[800px] tw-mx-auto phone:tw-px-[20px] phone:tw-w-full phone:tw-mt-[36px]'>
                        <div className='tw-w-full tw-mb-[60px]'>
                            <div className='tw-px-[20px] tw-py-[20px] tw-mb-[25px] tw-flex tw-items-center tw-bg-[#f5f5fa]'>
                                <div className='tw-ml-[8px] tw-font-bold'>{translations && translations['brand_model']}</div>
                            </div>
                            <div className='tw-w-full phone:tw-w-full pad:tw-w-full phone:tw-text-[14px] '>
                                <div className='tw-flex tw-items-center '>
                                    <div className='tw-w-[30%] tw-h-[60px] phone:tw-h-[100px] tw-py-[10px] tw-px-[30px] phone:tw-px-[10px] tw-border tw-flex tw-items-center tw-font-bold'><div className='tw-line-clamp-2 phone:tw-line-clamp-3 tw-break-words'>{translations && translations['brand_model']}</div></div>
                                    <div className='tw-w-[70%] tw-h-[60px] phone:tw-h-[100px] tw-py-[10px] tw-px-[30px] tw-border tw-flex tw-items-center'><div className='tw-line-clamp-2 phone:tw-line-clamp-3 tw-break-all'>{vehicleModel}</div></div>
                                </div>
                            </div>
                            <div className='tw-text-[18px] tw-pl-[30px] tw-py-[20px] tw-underline tw-italic tw-text-light-blue' onClick={() => this.routeToSpecPage(PagePathConstants.VEHICLE_MODEL)}>{translations && translations['update_info']}</div>
                        </div>

                        <div className='tw-w-full tw-mb-[60px]'>
                            <div className='tw-px-[20px] tw-py-[20px] tw-mb-[25px] tw-flex tw-items-center tw-bg-[#f5f5fa]'>
                                <div className='tw-ml-[8px] tw-font-bold'>{translations && translations['order_info']}</div>
                            </div>
                            <div className='tw-w-full phone:tw-w-full pad:tw-w-full phone:tw-text-[14px] '>
                                <div className='tw-flex tw-items-center '>
                                    <div className='tw-w-[30%] tw-h-[60px] phone:tw-h-[100px] tw-py-[10px] tw-px-[30px] phone:tw-px-[10px] tw-border tw-flex tw-items-center tw-font-bold'><div className='tw-line-clamp-2 phone:tw-line-clamp-3 tw-break-words'>{translations && translations['title_FN']}</div></div>
                                    <div className='tw-w-[70%] tw-h-[60px] phone:tw-h-[100px] tw-py-[10px] tw-px-[30px] tw-border tw-flex tw-items-center'><div className='tw-line-clamp-2 phone:tw-line-clamp-3 tw-break-all'>{firstName}</div></div>
                                </div>
                                <div className='tw-flex tw-items-center '>
                                    <div className='tw-w-[30%] tw-h-[60px] phone:tw-h-[100px] tw-py-[10px] tw-px-[30px] phone:tw-px-[10px] tw-border tw-flex tw-items-center tw-font-bold'><div className='tw-line-clamp-2 phone:tw-line-clamp-3 tw-break-words'>{translations && translations['title_LN']}</div></div>
                                    <div className='tw-w-[70%] tw-h-[60px] phone:tw-h-[100px] tw-py-[10px] tw-px-[30px] tw-border tw-flex tw-items-center'><div className='tw-line-clamp-2 phone:tw-line-clamp-3 tw-break-all'>{lastName}</div></div>
                                </div>
                                <div className='tw-flex tw-items-center '>
                                    <div className='tw-w-[30%] tw-h-[60px] phone:tw-h-[100px] tw-py-[10px] tw-px-[30px] phone:tw-px-[10px] tw-border tw-flex tw-items-center tw-font-bold'><div className='tw-line-clamp-2 phone:tw-line-clamp-3 tw-break-words'>{translations && translations['title_EA']}</div></div>
                                    <div className='tw-w-[70%] tw-h-[60px] phone:tw-h-[100px] tw-py-[10px] tw-px-[30px] tw-border tw-flex tw-items-center'><div className='tw-line-clamp-2 phone:tw-line-clamp-3 tw-break-all'>{email}</div></div>
                                </div>
                                <div className='tw-flex tw-items-center '>
                                    <div className='tw-w-[30%] tw-h-[60px] phone:tw-h-[100px] tw-py-[10px] tw-px-[30px] phone:tw-px-[10px] tw-border tw-flex tw-items-center tw-font-bold'><div className='tw-line-clamp-2 phone:tw-line-clamp-3 tw-break-words'>{translations && translations['title_DEA']}</div></div>
                                    <div className='tw-w-[70%] tw-h-[60px] phone:tw-h-[100px] tw-py-[10px] tw-px-[30px] tw-border tw-flex tw-items-center'><div className='tw-line-clamp-2 phone:tw-line-clamp-3 tw-break-all'>{driveEmail}</div></div>
                                </div>
                                <div className='tw-flex tw-items-center '>
                                    <div className='tw-w-[30%] tw-h-[60px] phone:tw-h-[100px] tw-py-[10px] tw-px-[30px] phone:tw-px-[10px] tw-border tw-flex tw-items-center tw-font-bold'><div className='tw-line-clamp-2 phone:tw-line-clamp-3 tw-break-words'>{translations && translations['title_PN']}</div></div>
                                    <div className='tw-w-[70%] tw-h-[60px] phone:tw-h-[100px] tw-py-[10px] tw-px-[30px] tw-border tw-flex tw-items-center'><div className='tw-line-clamp-2 phone:tw-line-clamp-3 tw-break-all'>{phone}</div></div>
                                </div>
                                <div className='tw-flex tw-items-center '>
                                    <div className='tw-w-[30%] tw-h-[60px] phone:tw-h-[100px] tw-py-[10px] tw-px-[30px] phone:tw-px-[10px] tw-border tw-flex tw-items-center tw-font-bold'><div className='tw-line-clamp-2 phone:tw-line-clamp-3 tw-break-words'>{translations && translations['title_DPN']}</div></div>
                                    <div className='tw-w-[70%] tw-h-[60px] phone:tw-h-[100px] tw-py-[10px] tw-px-[30px] tw-border tw-flex tw-items-center'><div className='tw-line-clamp-2 phone:tw-line-clamp-3 tw-break-all'>{drivePhone}</div></div>
                                </div>
                                <div className='tw-flex tw-items-center '>
                                    <div className='tw-w-[30%] tw-h-[60px] phone:tw-h-[100px] tw-py-[10px] tw-px-[30px] phone:tw-px-[10px] tw-border tw-flex tw-items-center tw-font-bold'><div className='tw-line-clamp-2 phone:tw-line-clamp-3 tw-break-words'>{translations && translations['title_State']}</div></div>
                                    <div className='tw-w-[70%] tw-h-[60px] phone:tw-h-[100px] tw-py-[10px] tw-px-[30px] tw-border tw-flex tw-items-center'><div className='tw-line-clamp-2 phone:tw-line-clamp-3 tw-break-all'>{state}</div></div>
                                </div>
                                <div className='tw-flex tw-items-center '>
                                    <div className='tw-w-[30%] tw-h-[60px] phone:tw-h-[100px] tw-py-[10px] tw-px-[30px] phone:tw-px-[10px] tw-border tw-flex tw-items-center tw-font-bold'><div className='tw-line-clamp-2 phone:tw-line-clamp-3 tw-break-words'>{translations && translations['title_City']}</div></div>
                                    <div className='tw-w-[70%] tw-h-[60px] phone:tw-h-[100px] tw-py-[10px] tw-px-[30px] tw-border tw-flex tw-items-center'><div className='tw-line-clamp-2 phone:tw-line-clamp-3 tw-break-all'>{city}</div></div>
                                </div>
                                <div className='tw-flex tw-items-center '>
                                    <div className='tw-w-[30%] tw-h-[60px] phone:tw-h-[100px] tw-py-[10px] tw-px-[30px] phone:tw-px-[10px] tw-border tw-flex tw-items-center tw-font-bold'><div className='tw-line-clamp-2 phone:tw-line-clamp-3 tw-break-words'>{translations && translations['title_Address']}</div></div>
                                    <div className='tw-w-[70%] tw-h-[60px] phone:tw-h-[100px] tw-py-[10px] tw-px-[30px] tw-border tw-flex tw-items-center'><div className='tw-line-clamp-2 phone:tw-line-clamp-3 tw-break-all'>{address}</div></div>
                                </div>
                                <div className='tw-flex tw-items-center '>
                                    <div className='tw-w-[30%] tw-h-[60px] phone:tw-h-[100px] tw-py-[10px] tw-px-[30px] phone:tw-px-[10px] tw-border tw-flex tw-items-center tw-font-bold'><div className='tw-line-clamp-2 phone:tw-line-clamp-3 tw-break-words'>{translations && translations['title_ZCode']}</div></div>
                                    <div className='tw-w-[70%] tw-h-[60px] phone:tw-h-[100px] tw-py-[10px] tw-px-[30px] tw-border tw-flex tw-items-center'><div className='tw-line-clamp-2 phone:tw-line-clamp-3 tw-break-all'>{zipCode}</div></div>
                                </div>
                                <div className='tw-flex tw-items-center '>
                                    <div className='tw-w-[30%] tw-h-[60px] phone:tw-h-[100px] tw-py-[10px] tw-px-[30px] phone:tw-px-[10px] tw-border tw-flex tw-items-center tw-font-bold'><div className='tw-line-clamp-2 phone:tw-line-clamp-3 tw-break-words'>{translations && translations['title_Country']}</div></div>
                                    <div className='tw-w-[70%] tw-h-[60px] phone:tw-h-[100px] tw-py-[10px] tw-px-[30px] tw-border tw-flex tw-items-center'><div className='tw-line-clamp-2 phone:tw-line-clamp-3 tw-break-all'>{country}</div></div>
                                </div>
                                {country && country === 'US' && <div className='tw-flex tw-items-center '>
                                    <div className='tw-w-[30%] tw-h-[60px] phone:tw-h-[100px] tw-py-[10px] tw-px-[30px] phone:tw-px-[10px] tw-border tw-flex tw-items-center tw-font-bold'><div className='tw-line-clamp-2 phone:tw-line-clamp-3 tw-break-words'>{translations && translations['title_sn']}</div></div>
                                    <div className='tw-w-[70%] tw-h-[60px] phone:tw-h-[100px] tw-py-[10px] tw-px-[30px] tw-border tw-flex tw-items-center'><div className='tw-line-clamp-2 phone:tw-line-clamp-3 tw-break-all'>{snCode}</div></div>
                                </div>}

                            </div>

                            <div className='tw-text-[18px] tw-pl-[30px] tw-py-[20px] tw-underline tw-italic tw-text-light-blue' onClick={() => this.routeToSpecPage(PagePathConstants.CUSTOM_INFO)}>{translations && translations['update_info']}</div>
                        </div>

                        <div className='tw-w-full tw-mb-[60px]'>
                            <div className='tw-px-[20px] tw-py-[20px] tw-mb-[25px] tw-flex tw-items-center tw-bg-[#f5f5fa]'>
                                <div className='tw-ml-[8px] tw-font-bold'>{translations && translations['id_info']}</div>
                            </div>
                            <div className='tw-w-full phone:tw-w-full pad:tw-w-full phone:tw-text-[14px] '>
                                <div className='tw-flex tw-items-center '>
                                    <div className='tw-w-[30%] tw-h-[60px] phone:tw-h-[100px] tw-py-[10px] tw-px-[30px] phone:tw-px-[10px] tw-border tw-flex tw-items-center tw-font-bold'><div className='tw-line-clamp-2 phone:tw-line-clamp-3 tw-break-words'>{translations && translations['id_type']}</div></div>
                                    <div className='tw-w-[70%] tw-h-[60px] phone:tw-h-[100px] tw-py-[10px] tw-px-[30px] tw-border tw-flex tw-items-center'><div className='tw-line-clamp-2 phone:tw-line-clamp-3 tw-break-all'>{identityType}</div></div>
                                </div>
                                <div className='tw-flex tw-items-center '>
                                    <div className='tw-w-[30%] tw-h-[320px] tw-py-[10px] tw-px-[30px] phone:tw-px-[10px] tw-border tw-flex tw-items-center tw-font-bold'><div className='tw-line-clamp-2 phone:tw-line-clamp-3 tw-break-words'>{translations && translations['front_of_doc']}</div></div>
                                    <div className='tw-w-[70%] tw-h-[320px] tw-py-[10px] tw-px-[30px] tw-border tw-flex tw-items-center'> <div className='tw-w-[500px] tw-h-[300px] pad:tw-w-[300px] pad:tw-h-[200px] phone:tw-w-[200px] phone:tw-h-[150px]' style={{ backgroundImage: `url(${documentFront})`, backgroundSize: '100%', backgroundRepeat: 'no-repeat', backgroundPosition: 'center' }}></div> </div>
                                </div>
                                {orderInfo && orderInfo.identityType !== StoreStateConstants.IDENTITY_TYPE_PASSPORT && <div className='tw-flex tw-items-center '>
                                    <div className='tw-w-[30%] tw-h-[320px] tw-py-[10px] tw-px-[30px] phone:tw-px-[10px] tw-border tw-flex tw-items-center tw-font-bold'><div className='tw-line-clamp-2 phone:tw-line-clamp-3 tw-break-words'>{translations && translations['back_of_doc']}</div></div>
                                    <div className='tw-w-[70%] tw-h-[320px] tw-py-[10px] tw-px-[30px] tw-border tw-flex tw-items-center'> <div className='tw-w-[500px] tw-h-[300px]  pad:tw-w-[300px] pad:tw-h-[200px] phone:tw-w-[200px] phone:tw-h-[150px]' style={{ backgroundImage: `url(${documentBack})`, backgroundSize: '100%', backgroundRepeat: 'no-repeat', backgroundPosition: 'center' }}></div> </div>
                                </div>}
                                <div className='tw-flex tw-items-center '>
                                    <div className='tw-w-[30%] tw-h-[500px] tw-py-[10px] tw-px-[30px] phone:tw-px-[10px] tw-border tw-flex tw-items-center tw-font-bold'><div className='tw-line-clamp-2 phone:tw-line-clamp-3 tw-break-words'>{translations && translations['selfie_video']}</div></div>
                                    <div className='tw-w-[70%] tw-h-[500px] tw-py-[10px] tw-px-[30px] tw-border tw-flex tw-items-center'>
                                        <video id="video" width={400} height={300} src={userSelfPictureOrVideo} playsInline="true" webkit-playsinline="true" controls></video>
                                    </div>
                                </div>
                            </div>
                            <div className='tw-text-[18px] tw-pl-[30px] tw-py-[20px] tw-underline tw-italic tw-text-light-blue' onClick={() => this.routeToSpecPage(PagePathConstants.DOCUMENT_INFO)}>{translations && translations['update_info']}</div>
                        </div>

                        <div className='tw-w-full tw-mt-[50px]'>
                            <div className='tw-text-[#5c5e62] tw-font-[500] tw-text-[28px] phone:tw-text-[22px]'>{translations && translations.title_ChooseAP}</div>
                            {this.state.insurance_types.map((item) => {
                                return <div className='tw-mt-[10px] tw-border-border-gray tw-border tw-px-[10px] tw-py-[10px] tw-mb-[10px] tw-rounded-[10px] tw-flex tw-items-center' key={item.key} onClick={() => this.handleSelectPayType(item.key)}>
                                    <RDSRadioButton
                                        name="grade-subject-selector"
                                        checked={item.key === selectedInsuranceType}
                                        id="grade-subject-radio"
                                    />
                                    <span className='tw-ml-[15px] tw-items-center tw-text-[14px] tw-font-[500]' dangerouslySetInnerHTML={{ __html: item.label }}></span>
                                </div>
                            })}
                        </div>

                        <div className='tw-w-full tw-flex tw-justify-center tw-items-center tw-mt-[80px] tw-text-white tw-mt-[50px]'>
                                <RDSButton
                                    color="primary"
                                    onClick={this.handleSubmitForm}
                                    disabled={disabledSubmitBtn}
                                    loading={isSubmitting}>
                                    <span>Submit</span>
                                </RDSButton>
                            </div>
                    </div>

                </div>
            </div >
        );
    }
}

function mapStateToProps(state) {
    return {
        translations: TranslationSelectors.selectTranslations(state),
    };
}

function mapDispatchToProps(dispatch) {
    return {
        orderActions: bindActionCreators(orderActionCreator, dispatch),
    };
}

export default connect(mapStateToProps, mapDispatchToProps)(OrderConfirmationPage);
