import React, { Component } from 'react';
import queryString from "query-string";
import { connect } from 'react-redux';
import { bindActionCreators } from 'redux';
import ReactPhoneInput from 'react-telephone-input/lib/withStyles';
import TeslaIcon from '../../icons/TeslaIcon';
import ReactFlagsSelect from 'react-flags-select';
import EDSInput from '../../components/common/EDSInput';
import RDSButton from '../../components/common/RDSButton';
import RdsBackIcon from '../../icons/RdsBackIcon';
import Loading from '../../components/common/LoadingIndicator';
import Checkbox from '../../components/common/Checkbox';
import * as FormValidationsUtil from '../../utils/FormValidationsUtil';
import * as UserLocationUtil from '../../utils/UserLocationUtil';
import * as imgActionCreator from '../../actions/imgActionCreator';
import * as orderActionCreator from '../../actions/orderActionCreator';
import * as globalAlertActionCreator from '../../actions/globalAlertActionCreator';
import I18N from '../../i18n/index';
import * as TranslationSelectors from '../../selectors/TranslationSelectors';

import flagImg from '../../images/flags.png';
import LocalStorageUtil from '../../utils/LocalStorageUtil';
import PagePathConstants from '../../constants/router/PagePathConstants';
import axios from 'axios';


const MAX_SSN_LENGTH = 9;

class CustomInfoPage extends Component {
    constructor(props, context) {
        super(props, context);
        this.state = {
            firstName: '',
            lastName: '',
            phone: '+1',
            email: '',

            drivePhone: '+1',
            driveEmail: '',

            state: '',
            city: '',
            address: '',
            zipCode: '',
            selectedCountryCode: 'US',
            snCode: '',
            isAcceptPromise: false,

            showError: false,
            errorMsg: '',
        };

        this.handlePhoneChange = this.handlePhoneChange.bind(this);
        this.handleEmailChange = this.handleEmailChange.bind(this);

        this.handleDrivePhoneChange = this.handleDrivePhoneChange.bind(this);
        this.handleDriveEmailChange = this.handleDriveEmailChange.bind(this);

        this.handleFristNameChange = this.handleFristNameChange.bind(this);
        this.handleLastNameChange = this.handleLastNameChange.bind(this);
        this.handleStateChange = this.handleStateChange.bind(this);
        this.handleCityChange = this.handleCityChange.bind(this);
        this.handleAddressChange = this.handleAddressChange.bind(this);
        this.handleZipCodeChange = this.handleZipCodeChange.bind(this);
        this.handleAccpetPromise = this.handleAccpetPromise.bind(this);
        this.handleSNCodeChange = this.handleSNCodeChange.bind(this);
        this.onSelectCountry = this.onSelectCountry.bind(this);
        this.handleSubmitForm = this.handleSubmitForm.bind(this);
        this.goBack = this.goBack.bind(this);
        this.prefillOrderParams = this.prefillOrderParams.bind(this);
        this.fetchCountryCode = this.fetchCountryCode.bind(this);
    }

    componentDidMount() {
        this.prefillOrderParams();
        this.fetchCountryCode();
    }

    fetchCountryCode() {
        const _that = this;
        axios.get('https://ipapi.co/json/')
            .then(res => {
                if (res && res.data && res.data.country_code) {
                    _that.setState({selectedCountryCode: res.data.country_code});
                }
            })
            .catch(err => console.error(err));
    }

    prefillOrderParams() {
        const { location } = this.props;
        const queryParams = queryString.parse(location.search);
        const orderInfo = LocalStorageUtil.loadOrderDetail();

        const setOrderParams = (params, cb) => {
            const { firstName, lastName, email, phone, driveEmail, drivePhone, state, snCode, city, country, address, zipCode } = params;
            this.setState({
                firstName: firstName ? firstName : this.state.firstName,
                lastName: lastName ? lastName : this.state.lastName,
                email: email ? email : this.state.email,
                driveEmail: driveEmail ? driveEmail : this.state.driveEmail,
                state: state ? state : this.state.state,
                snCode: snCode ? snCode : this.state.snCode,
                city: city ? city : this.state.city,
                address: address ? address : this.state.address,
                zipCode: zipCode ? zipCode : this.state.zipCode,
                selectedCountryCode: country ? country : this.state.selectedCountryCode,
                phone: phone ? phone : this.state.phone,
                drivePhone: drivePhone ? drivePhone : this.state.drivePhone,
            }, () => {
                cb && cb();
                const { firstName, lastName, email, phone, state, city, address, zipCode } = this.state;
                const showIsAcceptPromise = (firstName && firstName.trim() !== '') && (lastName && lastName.trim() !== '') && (email && email.trim() !== '') && (phone && phone.trim() !== '') && (state && state.trim() !== '') && (city && city.trim() !== '') && (address && address.trim() !== '') && (zipCode && zipCode.trim() !== '');
                if (showIsAcceptPromise) {
                    this.setState({ isAcceptPromise: true });
                }
            });
        }
        const successCallBack = () => {
            if (queryParams.firstName && queryParams.firstName.trim() !== '') {
                setOrderParams(queryParams);
            }
        }
        if (orderInfo.firstName && orderInfo.firstName.trim() !== '') {
            setOrderParams(orderInfo, successCallBack);
        } else {
            if (queryParams.firstName && queryParams.firstName.trim() !== '') {
                setOrderParams(queryParams);
            }
        }
    }

    goBack() {
        const { history } = this.props;
        history && history.goBack();
    }

    handleAccpetPromise(e) {
        const checked = e.target.checked;
        this.setState({ isAcceptPromise: checked });
    }

    displayErrorMsg(errorMsg) {
        this.setState({ showError: true, errorMsg })
    }

    handleFristNameChange(event) {
        const firstName = event.currentTarget.value;
        this.setState({ firstName, showError: false });
    }

    handleLastNameChange(event) {
        const lastName = event.currentTarget.value;
        this.setState({ lastName, showError: false });
    }

    handlePhoneChange(phone) {
        this.setState({ phone, drivePhone: phone, showError: false });
    }

    handleEmailChange(event) {
        const email = event.currentTarget.value;
        this.setState({ email, driveEmail: email, showError: false });
    }

    handleDrivePhoneChange(drivePhone) {
        this.setState({ drivePhone, showError: false });
    }

    handleDriveEmailChange(event) {
        const driveEmail = event.currentTarget.value;
        this.setState({ driveEmail, showError: false });
    }

    handleStateChange(event) {
        const state = event.currentTarget.value;
        this.setState({ state, showError: false });
    }

    handleCityChange(event) {
        const city = event.currentTarget.value;
        this.setState({ city, showError: false });
    }

    handleZipCodeChange(event) {
        const zipCode = event && event.target.value;
        const regx = new RegExp(/^([0-9]\d*)$/);
        if (regx.test(zipCode) || zipCode === '') {
            this.setState({ zipCode });
        }
        this.setState({ showError: false });
    }

    onSelectCountry(countryCode) {
        this.setState({
            selectedCountryCode: countryCode,
            snCode: ''
        });
    }

    handleSNCodeChange(event) {
        const snCode = event.currentTarget.value.trim();
        const regx = new RegExp(/^([0-9]\d*)$/);
        if (snCode.length <= MAX_SSN_LENGTH && (regx.test(snCode) || snCode === '')) {
            this.setState({ snCode });
        }
        this.setState({ showError: false });
    }

    handleAddressChange(event) {
        const address = event.currentTarget.value;
        this.setState({ address, showError: false });
    }

    handleSubmitForm() {
        const { email, driveEmail, phone, drivePhone, state, city, address, zipCode, firstName, lastName, snCode, selectedCountryCode } = this.state;
        if (!FormValidationsUtil.isValidEmail(email.trim())) {
            this.setState({
                showError: true,
                errorMsg: I18N.getText('invalid-email')
            });
            return;
        }
        if (!FormValidationsUtil.isValidEmail(driveEmail.trim())) {
            this.setState({
                showError: true,
                errorMsg: I18N.getText('invalid-driver-email')
            });
            return;
        }
        if (!FormValidationsUtil.isValidPhoneNumber(phone.trim())) {
            this.setState({
                showError: true,
                errorMsg: I18N.getText('invalid-phone')
            });
            return;
        }
        if (!FormValidationsUtil.isValidPhoneNumber(drivePhone.trim())) {
            this.setState({
                showError: true,
                errorMsg: I18N.getText('invalid-driver-phone')
            });
            return;
        }
        let formattedPhone = phone.replaceAll(' ', '-').trim();
        let formattedDrivePhone = drivePhone.replaceAll(' ', '-').trim();


        let data = {
            firstName,
            lastName,
            email,
            phone: formattedPhone,
            driveEmail,
            drivePhone: formattedDrivePhone,
            state,
            city,
            country: selectedCountryCode,
            address,
            zipCode,
        }
        if (selectedCountryCode === 'US') {
            data['snCode'] = snCode;
        }
        const { location, history } = this.props;
        LocalStorageUtil.saveOrderDetail(data);

        setTimeout(() => {
            history && history.push({ pathname: PagePathConstants.DOCUMENT_INFO, search: location.search });
        }, 500);
    }

    render() {
        const { showError, errorMsg, showLoading, firstName, lastName, email, zipCode, snCode, state, city, address, isAcceptPromise, phone, driveEmail, drivePhone, isSubmitting, selectedCountryCode } = this.state;
        const showIsAcceptPromise = (firstName && firstName.trim() !== '') && (lastName && lastName.trim() !== '') && (email && email.trim() !== '') && (phone && phone.trim() !== '') && (state && state.trim() !== '') && (city && city.trim() !== '') && (address && address.trim() !== '') && (zipCode && zipCode.trim() !== '');
        const disabledSubmitBtn = (firstName.trim() === '' || lastName.trim() === '' || email.trim() === '' || phone.trim() === '' || address.trim() === '' || state.trim() === '' || city.trim() === '' || zipCode.trim() === '' || !isAcceptPromise || selectedCountryCode.trim() === '' || (selectedCountryCode === 'US' && snCode.trim() === '') || drivePhone.trim() === '' || driveEmail.trim() === '')
        let translations = this.props.translations && this.props.translations['submission_page'];
        const countrySelected = (selectedCountryCode !== '');
        return (
            <div className='tw-w-full tw-h-full gotham-font tw-text-[#393c41] '>
                {showLoading && <Loading />}
                <div className='tw-w-full tw-h-full phone:tw-h-full tw-pb-[200px] tw-overflow-scroll'>
                    <div className='tw-z-[100] tw-w-full tw-h-[80px] tw-flex tw-justify-between'>
                        <div className='tw-w-1/3 tw-h-full tw-flex tw-items-center tw-pl-[30px]'><RdsBackIcon color='#171a20' onClick={this.goBack} /></div>
                        <TeslaIcon size={150} color='#171a20' />
                        <div className='tw-w-1/3'></div>
                    </div>
                    <div className='tw-mt-[50px] tw-w-[660px] tw-mx-auto phone:tw-px-[20px] phone:tw-w-full phone:tw-mt-[36px]'>
                        <div className='tw-font-[500] tw-text-[28px] phone:tw-text-[22px]'>{translations && translations.title_CI}</div>
                        <div className='tw-mt-[20px]'>
                            <div className='tw-w-full tw-mt-[24px] tw-flex phone:tw-flex-wrap tw-items-center tw-flex tw-justify-between tw-items-center'>
                                <div className='tw-w-[47%] phone:tw-w-full tw-h-full tw-flex tw-flex-col tw-justify-between phone:tw-mb-[20px]'>
                                    <div className='tw-text-[#5c5e62] tw-text-[14px] tw-font-[500]'>{translations && translations.title_FN}</div>
                                    <div className='tw-mt-[10px]'>
                                        <EDSInput className="tw-w-full tw-h-[40px] tw-bg-[#f4f4f4] tw-border-none" value={firstName} onChange={this.handleFristNameChange} />
                                    </div>
                                </div>
                                <div className='tw-w-[47%] phone:tw-w-full tw-h-full tw-flex tw-flex-col tw-justify-between phone:tw-mb-[20px]'>
                                    <div className='tw-text-[#5c5e62] tw-text-[14px] tw-font-[500]'>{translations && translations.title_LN}</div>
                                    <div className='tw-mt-[10px]'>
                                        <EDSInput className="tw-w-full tw-h-[40px] tw-bg-[#f4f4f4] tw-border-none" value={lastName} onChange={this.handleLastNameChange} />
                                    </div>
                                </div>
                            </div>
                            <div className='tw-w-full tw-mt-[24px] phone:tw-mt-0 tw-flex phone:tw-flex-wrap tw-items-center tw-flex tw-justify-between tw-items-center' >
                                <div className='tw-w-[47%] phone:tw-w-full tw-h-full tw-flex tw-flex-col tw-justify-between phone:tw-mb-[20px]'>
                                    <div className='tw-text-[#5c5e62] tw-text-[14px] tw-font-[500]'>{translations && translations.title_EA}</div>
                                    <div className='tw-mt-[10px]'>
                                        <EDSInput className="tw-w-full tw-h-[40px] tw-bg-[#f4f4f4] tw-border-none"
                                            onChange={this.handleEmailChange} value={email} />
                                    </div>
                                </div>
                                <div className='tw-w-[47%] phone:tw-w-full tw-h-full tw-flex tw-flex-col tw-justify-between phone:tw-mb-[20px]'>
                                    <div className='tw-text-[#5c5e62] tw-text-[14px] tw-font-[500]'>{translations && translations.title_PN}</div>
                                    <div className='tw-mt-[10px]'>
                                        <ReactPhoneInput
                                            className="tw-w-full"
                                            defaultCountry="us"
                                            value={phone}
                                            flagsImagePath={flagImg}
                                            onChange={this.handlePhoneChange}
                                        />
                                    </div>
                                </div>
                            </div>
                            <div className='tw-w-full tw-mt-[24px] phone:tw-mt-0 tw-flex phone:tw-flex-wrap tw-items-center tw-flex tw-justify-between tw-items-center'>
                                <div className='tw-w-[47%] phone:tw-w-full tw-h-full tw-flex tw-flex-col tw-justify-between phone:tw-mb-[20px]'>
                                    <div className='tw-text-[#5c5e62] tw-text-[14px] tw-font-[500]'>{translations && translations.title_State}</div>
                                    <div className='tw-mt-[10px]'>
                                        <EDSInput className="tw-w-full tw-h-[40px] tw-bg-[#f4f4f4] tw-border-none" value={state} onChange={this.handleStateChange} />
                                    </div>
                                </div>
                                <div className='tw-w-[47%] phone:tw-w-full tw-h-full tw-flex tw-flex-col tw-justify-between phone:tw-mb-[20px]'>
                                    <div className='tw-text-[#5c5e62] tw-text-[14px] tw-font-[500]'>{translations && translations.title_City}</div>
                                    <div className='tw-mt-[10px]'>
                                        <EDSInput className="tw-w-full tw-h-[40px] tw-bg-[#f4f4f4] tw-border-none" value={city} onChange={this.handleCityChange} />
                                    </div>
                                </div>
                            </div>
                            <div className='tw-w-full tw-mt-[24px] phone:tw-mt-0 tw-flex phone:tw-flex-wrap tw-items-center tw-flex tw-justify-between tw-items-center'>
                                <div className='tw-w-full phone:tw-w-full tw-h-full tw-flex tw-flex-col tw-justify-between phone:tw-mb-[20px]'>
                                    <div className='tw-text-[#5c5e62] tw-text-[14px] tw-font-[500]'>{translations && translations.title_Address}</div>
                                    <div className='tw-mt-[10px]'>
                                        <EDSInput className="tw-w-full tw-h-[40px] tw-bg-[#f4f4f4] tw-border-none" value={address} onChange={this.handleAddressChange} />
                                    </div>
                                </div>
                            </div>
                            <div className='tw-w-full tw-mt-[24px] phone:tw-mt-0 tw-flex phone:tw-flex-wrap tw-items-center tw-flex tw-justify-between tw-items-center'>
                                <div className='tw-w-[100%] tw-h-full tw-flex tw-flex-col tw-justify-between'>
                                    <div className='tw-text-[#5c5e62] tw-text-[14px] tw-font-[500]'>{translations && translations.title_ZCode} <span>({translations && translations.description_ZCode})</span></div>
                                    <div className='tw-mt-[10px] tw-w-full'>
                                        <EDSInput className="tw-w-[47%] phone:tw-w-full tw-h-[40px] tw-bg-[#f4f4f4] tw-border-none" value={zipCode} onChange={this.handleZipCodeChange} />
                                    </div>
                                </div>
                            </div>

                            {showIsAcceptPromise && <div className='tw-w-full tw-mt-[24px] tw-flex tw-items-center tw-flex tw-justify-between tw-items-center'>
                                <Checkbox
                                    checked={isAcceptPromise}
                                    onChange={this.handleAccpetPromise}
                                    label={null}
                                />
                                <div className="hs-eula-tip tw-text-body-text-1 tw-text-[14px] tw-leading-[25px]">{translations && translations.promise_text}</div>
                            </div>}

                            {isAcceptPromise && <React.Fragment>
                                <div className='tw-w-full tw-mt-[24px] tw-flex phone:tw-flex-wrap tw-items-center tw-flex tw-justify-between tw-items-center' >
                                    <div className='tw-w-[47%] phone:tw-w-full tw-h-full tw-flex tw-flex-col tw-justify-between'>
                                        <div className='tw-text-[#5c5e62] tw-text-[14px] tw-font-[500]'>{translations && translations.title_Country}</div>
                                        <div className='tw-mt-[10px] tw-w-full hs-country-selector'>
                                            <ReactFlagsSelect
                                                className={`country-selector ${countrySelected ? '' : 'no-country'} `}
                                                selected={selectedCountryCode}
                                                placeholder={translations && translations.placeholder_Country}
                                                onSelect={this.onSelectCountry}
                                                searchable
                                                searchPlaceholder={translations && translations.search_Country}
                                                customLabels={UserLocationUtil.getCountryNames(I18N.getText)}
                                            />
                                        </div>
                                    </div>

                                    <div className='tw-w-[47%] phone:tw-w-full tw-h-full tw-flex tw-flex-col tw-justify-between phone:tw-mb-[20px]'>
                                        {selectedCountryCode === 'US' && <div className='tw-w-[100%] tw-h-full tw-flex tw-flex-col tw-justify-between'>
                                            <div className='tw-text-[#5c5e62] tw-text-[14px] tw-font-[500]'>{translations && translations.title_sn}</div>
                                            <div className='tw-mt-[12px] tw-w-full'>
                                                <EDSInput className="tw-w-full phone:tw-w-full tw-h-[42px] tw-bg-[#f4f4f4] tw-border-none" value={snCode} onChange={this.handleSNCodeChange} />
                                            </div>
                                        </div>}
                                    </div>
                                </div>


                                <div className='tw-w-full tw-mt-[24px] tw-flex phone:tw-flex-wrap tw-items-center tw-flex tw-justify-between tw-items-center' >
                                    <div className='tw-w-[47%] phone:tw-w-full tw-h-full tw-flex tw-flex-col tw-justify-between phone:tw-mb-[20px]'>
                                        <div className='tw-text-[#5c5e62] tw-text-[14px] tw-font-[500]'>{translations && translations.title_DEA}</div>
                                        <div className='tw-mt-[10px]'>
                                            <EDSInput className="tw-w-full tw-h-[40px] tw-bg-[#f4f4f4] tw-border-none"
                                                onChange={this.handleDriveEmailChange} value={driveEmail} />
                                        </div>
                                    </div>
                                    <div className='tw-w-[47%] phone:tw-w-full tw-h-full tw-flex tw-flex-col tw-justify-between phone:tw-mb-[20px]'>
                                        <div className='tw-text-[#5c5e62] tw-text-[14px] tw-font-[500]'>{translations && translations.title_DPN}</div>
                                        <div className='tw-mt-[10px]'>
                                            <ReactPhoneInput
                                                className="tw-w-full"
                                                defaultCountry="us"
                                                value={drivePhone}
                                                flagsImagePath={flagImg}
                                                onChange={this.handleDrivePhoneChange}
                                            />
                                        </div>
                                    </div>
                                </div>

                            </React.Fragment>}

                            {showError && <div className='tw-text-light-red tw-my-[30px] tw-px-[20px]'>{errorMsg}</div>}
                            <div className='tw-w-full tw-flex tw-justify-center tw-items-center tw-mt-[30px] tw-text-white tw-mt-[50px]'>
                                <RDSButton
                                    color="primary"
                                    onClick={this.handleSubmitForm}
                                    disabled={disabledSubmitBtn}
                                    loading={isSubmitting}>
                                    <span>Next</span>
                                </RDSButton>
                            </div>
                        </div>
                    </div>
                </div>
            </div >
        );
    }
}

function mapStateToProps(state) {
    return {
        translations: TranslationSelectors.selectTranslations(state),
    };
}

function mapDispatchToProps(dispatch) {
    return {
        imgActions: bindActionCreators(imgActionCreator, dispatch),
        orderActions: bindActionCreators(orderActionCreator, dispatch),
        globalAlertActions: bindActionCreators(globalAlertActionCreator, dispatch),
    };
}

export default connect(mapStateToProps, mapDispatchToProps)(CustomInfoPage);
