import React, { Component } from 'react';
import queryString from "query-string";
import TeslaIcon from '../icons/TeslaIcon';
import TeslaImg from '../images/tesla.png';
import WhatsAppIcon from '../images/scoial/icon_WhatsApp@2x.png';
import SmsIcon from '../images/scoial/icon_sms.png';
import TelegramIcon from '../images/scoial/icon_Telegram.webp';
import RDSButton from '../components/common/RDSButton';
import PagePathConstants from '../constants/router/PagePathConstants';
import MobileDetectUtil from '../utils/MobileDetectUtil';
import { Progress } from 'reactstrap';

const HTTP_REQUEST_URL = window.location.protocol + "//" + window.location.host + '/#/?ad=share';
class SuccessfullyPage extends Component {
    constructor(props, context) {
        super(props, context);
        const invitation_text = 'How to get that free tesla for one year~';
        this.state = {
            state: -1,
            email: '',
            phone: '',
            successfullyText: '',
            failedText: '',
            navList: [
                {
                    title: 'WhatsApp',
                    img: WhatsAppIcon,
                    url: `https://api.whatsapp.com/send?text=${encodeURIComponent(`${invitation_text} ${HTTP_REQUEST_URL}`)}&url=${encodeURIComponent(HTTP_REQUEST_URL)}`,
                    isClick: false,
                },
                {
                    title: 'Sms',
                    img: SmsIcon,
                    url: `sms:${MobileDetectUtil.judgeClient() === 'iOS' ? '&' : '?'}body=${invitation_text} ${HTTP_REQUEST_URL}`,
                    isClick: false,
                },
                {
                    title: 'Telegram',
                    img: TelegramIcon,
                    url: `https://telegram.me/share/url?text=${invitation_text}&url=${encodeURIComponent(HTTP_REQUEST_URL)}`,
                    isClick: false,
                },
                {
                    title: 'Twitter',
                    img: "data:image/png;base64,",
                    url: `https://twitter.com/intent/tweet?lang=en&text=${invitation_text}&url=${encodeURIComponent(HTTP_REQUEST_URL)}`,
                    isClick: false,
                },
                {
                    title: 'Facebook',
                    img: 'data:image/png;base64,',
                    url: `https://www.facebook.com/dialog/share?app_id=407682420960&display=popup&href=${encodeURIComponent(HTTP_REQUEST_URL)}`,
                    isClick: false,
                },
            ],
        }
        this.backToHome = this.backToHome.bind(this);
        this.shareLinkToApp = this.shareLinkToApp.bind(this);
    }

    componentDidMount() {
        const { location } = this.props;
        const queryParams = queryString.parse(location.search);
        console.log(queryParams);
        let successfullyText = '';
        if (queryParams.origin === 'payment') {
            successfullyText = 'Thank you for your order! We will send an email to %email to follow up on the order status. At the same time, we will text your phone number %phone within 5 business days, please pay attention to answer it.';
        } else {
            successfullyText = 'Thank you for your submission! We will send an email to %email to follow up on the submission status. At the same time, we will text your phone number %phone within 5 business days, please pay attention to answer it.';
        }
        let failedText = '';
        if (queryParams.origin === 'payment') {
            failedText = 'Fail to pay! please try again later.';
        } else {
            failedText = 'Fail to submit! please try again later.'
        }
        successfullyText = successfullyText.replace('%email', `<span class="tw-text-normal-blue">${queryParams.email}</span>`).replace('%phone', `<span class="tw-text-normal-blue">${queryParams.phone}</span>`)
        this.setState({
            state: parseInt(queryParams.state),
            email: queryParams.email,
            phone: queryParams.phone,
            successfullyText,
            failedText
        });
    }

    backToHome() {
        const { history } = this.props;
        history && history.push(PagePathConstants.HOME_PAGE);
    }

    shareLinkToApp(item, index) {
        let newNavList = [].concat(this.state.navList);
        newNavList[index].isClick = true;
        this.setState({ navList: newNavList });
        console.log(MobileDetectUtil.judgeClient());
        console.log(item.url);
        window.open(item.url, "_blank");   // 打开新窗口
    }

    render() {
        const { state, successfullyText, failedText, navList } = this.state;
        const progress = (navList.filter(item => item.isClick).length / navList.length) * 100;
        return (
            <div className='tw-w-full tw-h-full gotham-font tw-text-[#393c41] '>
                <div className='tw-w-full tw-h-full phone:tw-h-full tw-overflow-scroll'>
                    <div className='tw-z-[100] tw-w-full tw-h-[80px] phone:tw-h-[60px] tw-flex tw-justify-between'>
                        <div className='tw-w-1/3'></div>
                        <TeslaIcon size={150} color='#171a20' />
                        <div className='tw-w-1/3'></div>
                    </div>
                    <div className='tw-mt-[30px] tw-w-[750px] phone:tw-w-full tw-mx-auto phone:tw-px-[20px] phone:tw-w-full phone:tw-mt-[36px]'>
                        {state === 1 && <div className='tw-flex tw-flex-col tw-justify-between tw-items-center'>
                            <img src={TeslaImg} alt='tesla' className='tw-w-[120px] tw-h-[120px] phone:tw-w-[80px] phone:tw-h-[80px]' />
                            <div className='tw-w-full tw-text-[18px] phone:tw-text-[14px] tw-font-[500] tw-text-center  phone:tw-text-left tw-mt-[30px]' dangerouslySetInnerHTML={{ __html: successfullyText }}></div>
                        </div>}
                        {state === 0 && <div className='tw-flex tw-flex-col tw-justify-between tw-items-center'>
                            <img src={TeslaImg} alt='tesla' className='tw-w-[120px] tw-h-[120px] phone:tw-w-[80px] phone:tw-h-[80px]' />
                            <div className='tw-w-full tw-text-[18px] phone:tw-text-[14px] tw-font-[500] tw-text-light-red tw-text-center  phone:tw-text-left'>{failedText}</div>
                        </div>}

                        <div className='tw-w-full tw-mt-[30px]'>
                            <div className='tw-text-[18px] phone:tw-text-[14px] tw-font-[500] tw-text-center phone:tw-text-left'>Share on social media and invite friends for a test drive to speed up the review of your information!</div>
                            <div className='phone:tw-mt-[15px] phone:tw-mb-[15px] tw-mt-[30px] tw-mb-[50px] tw-w-full tw-flex tw-justify-center phone:tw-justify-between  tw-items-center tw-flex-wrap'>
                                {navList.map((item, index) => {
                                    return (<div target='__blank' className='tw-inline-block tw-w-[70px] tw-h-[70px] tw-mr-[20px] phone:tw-mr-0 phone:tw-w-[50px] phone:tw-h-[50px] tw-rounded-full tw-mt-[10px] tw-mr-[10px]' onClick={() => this.shareLinkToApp(item, index)} key={item.title}>
                                        <img src={item.img} alt='app_icon' className='tw-w-full tw-h-full' />
                                    </div>)
                                })}
                            </div>
                            {progress > 0 && <div className='tw-mt-[30px] tw-w-full tw-h-[20px] tw-bg-[#f7f7f7] tw-relative tw-rounded-[10px] tw-overflow-hidden'>
                                <Progress className='tw-absolute tw-left-0 tw-top-0 tw-text-white tw-h-full  tw-text-[12px]' animated bar color="success" value={progress}>{`${progress}%`}</Progress>
                            </div>}
                        </div>
                        <div className='tw-w-full tw-flex tw-justify-center tw-items-center tw-mt-[30px] tw-text-white tw-mt-[20px]'>
                            <RDSButton
                                color="primary"
                                onClick={this.backToHome}>
                                <span>Back to Home</span>
                            </RDSButton>
                        </div>
                    </div>

                </div>
            </div>
        );
    }
}

export default SuccessfullyPage;
